
'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Home, Droplets, Wind, Thermometer, Lightbulb } from 'lucide-react';
import { cn } from '@/lib/utils';

const navItems = [
  { href: '/menu', icon: Home, label: 'MenÃº' },
  { href: '/water', icon: Droplets, label: 'Agua' },
  { href: '/humidity', icon: Wind, label: 'Humedad' },
  { href: '/temperature', icon: Thermometer, label: 'Temperatura' },
  { href: '/recommendations', icon: Lightbulb, label: 'Tips' },
];

export default function BottomNav() {
  const pathname = usePathname();

  // A little helper to determine the active path.
  // We consider /menu active if the current path is /status as well.
  const isActive = (href: string) => {
    if (href === '/menu') {
      return pathname === '/menu' || pathname === '/status';
    }
    return pathname === href;
  };

  return (
    <nav className="fixed bottom-0 left-0 right-0 bg-white shadow-[0_-2px_10px_rgba(0,0,0,0.05)]">
      <div className="flex justify-around max-w-md mx-auto px-2 py-3">
        {navItems.map((item) => (
          <Link href={item.href} key={item.href} passHref>
            <div
              className={cn(
                'flex flex-col items-center justify-center w-16 text-gray-500 transition-colors duration-200',
                isActive(item.href)
                  ? 'text-primary'
                  : 'hover:text-primary'
              )}
            >
              <item.icon
                className={cn(
                  'w-7 h-7 mb-1 transition-transform duration-200',
                  isActive(item.href) ? 'scale-110' : ''
                )}
              />
              <span className="text-xs font-medium">{item.label}</span>
            </div>
          </Link>
        ))}
      </div>
    </nav>
  );
}
